define('miume/models/youtube/playlist', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  var YoutubePlaylist;

  YoutubePlaylist = DS['default'].Model.extend({
    videos: DS['default'].hasMany("youtube/video", {
      embedded: true
    })
  });

  exports['default'] = YoutubePlaylist;

});